import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router';


import { Boletos } from '../Classes/boletos';
import { BoletoService } from './../shared/boleto.service';

@Component({
  selector: 'app-create-update',
  templateUrl: './create-update.component.html',
  styleUrls: ['./create-update.component.css']
})
export class CreateUpdateComponent implements OnInit {
  
  private boleto: Boletos
  
  constructor(
    private boletoService: BoletoService,
    private router:Router) { }
    
    ngOnInit()
    {
      this.boleto = this.boletoService.getter();
    }
    
    criarOrUpdate()
    {
      if(this.boleto._id == undefined)
      {
        this.boletoService.criarEmpresa(this.boleto).subscribe(
          data => {
            console.log(data);
            this.router.navigate(['/']);
          },
          error =>{
            console.log(error);
          }
        )
      }else{
        this.boletoService.atualizarBoletos(this.boleto).subscribe(
          data =>{
            console.log(data);
            this.router.navigate(['/']);
          },
          error => {
            console.log(error);
          }
        )
      }
    }    
  }
  


