import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router';


import { Boletos } from '../Classes/boletos';
import { BoletoService } from './../shared/boleto.service';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.css']
})
export class ListComponent implements OnInit {
  
  boletos: Boletos[];
  
  constructor(
    private _boletoService:BoletoService, private router:Router
  ) { }
  
  ngOnInit() 
  {
    this.lerBoletos();
  }
  
  lerBoletos()
  {
    this._boletoService.lerBoletos().subscribe(
      data =>{
        console.log(data);
        this.boletos = data['msg'];
      },
      error =>{
        console.log(error);
      }
    )
  }
  
  Update(boletos)
  {
    this._boletoService.setter(boletos);
    this.router.navigate(['/createUpdate']);
  }
  
  // detalhes(boletos)
  //{
  //   this._boletoService.detalhesBoletos(boletos._id).subscribe(
  //      data =>{
  //        console.log(data);
  //        this.boletos = data['msg'];
  //      },
  //      error =>{
  //        console.log(error);
  //      }
  //    )
  //  }
  
  detalhes(boletos)
  {
    this._boletoService.setter(boletos);
    this.router.navigate(['/detalhes'])
  }
  
  deletar(boletos)
  {
    this._boletoService.deletarBoletos(boletos._id).subscribe(
      data =>{
        this.boletos.splice(this.boletos.indexOf(boletos), 1);
      },
      error =>{
        console.log(error);
      }
    )
  }
}



