import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import 'rxjs/add/operator/map';

import {Boletos} from './../Classes/boletos';

@Injectable()
export class BoletoService {
  
  private boletos: Boletos;
  private baseUri: string = "http://localhost:5000";
  private headers = new HttpHeaders().set('Content-Type','application/json');
  
  constructor(private http: HttpClient) { }
  
  criarEmpresa(boletos: Boletos)
  {
    return this.http.post(this.baseUri+'/create', boletos,{headers: this.headers});
  }

  lerBoletos()
  {
    return this.http.get(this.baseUri+'/read',{headers:this.headers});
  }

  detalhesBoletos(id:string)
  {
    return this.http.get(this.baseUri+'/detalhes/'+id, {headers: this.headers});
  }

  atualizarBoletos(boletos: Boletos)
  {
    return this.http.put(this.baseUri+'/update', boletos,{headers: this.headers});
  }

  deletarBoletos(id:string)
  {
    return this.http.delete(this.baseUri+'/delete'+id,{headers:this.headers});
  }

  setter(boletos: Boletos)
  {
    this.boletos = boletos;
  }

  getter(){
    return this.boletos;
  }
  
}



