import { Component, OnInit } from '@angular/core';

import { BoletoService } from './../shared/boleto.service';
import { Boletos } from './../Classes/boletos';

@Component({
  selector: 'app-detalhes-boletos',
  templateUrl: './detalhes-boletos.component.html',
  styleUrls: ['./detalhes-boletos.component.css']
})
export class DetalhesBoletosComponent implements OnInit {
  
  boletos: Boletos;
  
  constructor(
    private boletoService:BoletoService
  ) { }
  
  ngOnInit() 
  {
    this.boletos = this.boletoService.getter();
  }
  
  detalhesBoletos(boletos)
  {
    this.boletoService.detalhesBoletos(boletos._id).subscribe(
      data => {
        console.log(data);
        this.boletos = data['msg'];
      },
      error => {
        console.log(error);
      }
    )
  }
}



