import { BoletoService } from './shared/boleto.service';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {FormsModule} from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import {RouterModule, Routes} from '@angular/router';


import { AppComponent } from './app.component';
import { NavbarComponent } from './navbar/navbar.component';
import { ListComponent } from './list/list.component';
import { CreateUpdateComponent } from './create-update/create-update.component';
import { DetalhesBoletosComponent } from './detalhes-boletos/detalhes-boletos.component';


const appRoutes: Routes = [
  {path: '', component: ListComponent},
  {path: 'createUpdate', component: CreateUpdateComponent},
  {path: 'detalhes', component: DetalhesBoletosComponent}
]

@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    ListComponent,
    CreateUpdateComponent,
    DetalhesBoletosComponent
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    FormsModule,
    RouterModule.forRoot(appRoutes)
  ],
  providers: [BoletoService],
  bootstrap: [AppComponent]
})
export class AppModule { }



